@import url('https://fonts.googleapis.com/css?family=Montserrat:700');

.hero {
  background-color: #0040C1;
  position: relative;
  height: 100vh;
  overflow: hidden;
  font-family: 'Montserrat', sans-serif;
}

.hero__title {
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 50px;
  z-index: 1;
}

.circle {
  position: fixed;
  top: 80vh;
  left: 45vw;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: solid 1px darken(#0040C1, 8%);
  transform-origin: top left;
  transform: scale(0) rotate(0deg) translate(-50%, -50%);
  animation: circle 6s ease-in forwards infinite;
  overflow: hidden;
  
  &:nth-child(2n) {
    border-color: lighten(#0040C1, 10%);
  }
  
  &:nth-child(2) {
    animation-delay: 1s;
    left: 25vw;
    top: 40vh;
  }
  
  &:nth-child(3) {
    animation-delay: 3s;
    left: 75vw;
    top: 50vh;
  }
  
  &:nth-child(4) {
    animation-delay: 5s;
    left: 90vw;
    top: 10vh;
  }
  
  &:nth-child(5) {
    animation-delay: 7s;
    left: 10vw;
    top: 60vh;
  }
  
  &:nth-child(6) {
    animation-delay: 9s;
    left: 50vw;
    top: 10vh;
  }

  &:nth-child(7){
    animation-delay: 11s;
    left: 30vh;
    top: 10vh;
  }
}

@keyframes circle {
  from {
    transform: scale(0)  translate(-50%, -50%);
    opacity: 1;
  }
  to {
    transform: scale(20) translate(-50%, -50%); 
    opacity: 0;
  }
}