a:hover,
a:active,
a:link {
    text-decoration: none;
}
.tiny-space {
    font-size: 1px;
}

.actively {
    border-bottom: 1px #ddd solid;
}
.container-home {
    padding-top: 1.2em;
}

.text-center {
    text-align: center;
    margin-right: 20%;
    margin-left: 20%;
}

@media only screen and (max-width: 800px) {
    .text-center{
        margin-left: 10%;
        margin-right: 10%;
    }
}

@media only screen and (max-width: 600px) {
    .text-center{
        margin-left: 0%;
        margin-right: 3%;
    }
}
.home-social {
    font-size: 32px;
    display: in-line;
    list-style-type: none;
    padding-left: 0px;
}
.home-social li {
    display: inline;
    margin-left: 10px;
    margin-right: 10px;
}
.contacts-social {
    font-size: 18px;
    list-style-type: none;
    padding-left: 0px;
}
