.slide-indicator {
    z-index: 1;
    transform: translateY(-50%);
    text-transform: uppercase;
    font-size: 20px;
    text-align: center;
    visibility: inherit;
    opacity: 1; 
    transform: matrix(1, 0, 0, 1, 0, 0);
    color:  #656565;
  }
  .slide-indicator ul{
    margin-block-start: 0em;
    margin-block-end: 0em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 0px;
  }
  .page__num{
      font-size: 90px;
  }

  .page__name{
    font-size: 20px;
  }

  @media only screen and (max-width: 600px) {
    .page__num{
        font-size: 60px;
    }
    .page__count{
      font-size: 15px;
    }
    .page__name{
      font-size: 15px;
    }
  }


  .separator{
      background-color: #656565;
      width: 100%;
      height: 0.1em;
  }
  .page__count{
      font-size: 30px;
  }
  .slide-indicator__current-slide {
    font-size: 3em;
    width: .2em;
  }
  .slide-indicator__separator {
    background-color: #656565;
    width: 40px;
    height: 0.2em;
  }
  .slide-indicator__slider-length {
    font-size: 1.5em;
  }

  .Score {
    position: fixed;
  }

  .Score--bounce {
    animation: Score-keyframes--bounce .8s ease-out
  }

  @keyframes Score-keyframes--bounce {
    0%,
    to {
      transform: rotate(0)
    }
    10% {
      transform: scale(1.1)
    }
    20% {
      transform: rotate(4deg) translateY(-12px)
    }
    40% {
      transform: rotate(2deg) translateY(0)
    }
    60% {
      transform: rotate(-2deg) translateY(-8px)
    }
    80% {
      transform: rotate(-1deg) translateY(0)
    }
  }

  .page-row{
    height: 100%;
    margin-left: 20px;
  }

  .page-row:after{
    display: table;
    clear: both;
  }

  .page-column{
    float: left;
    height: 100%;
  }
  .cleft{
    width: 15%;
  }
  .cright{
    margin-left: 20px;
    width: 78%;
  }