.card-carousel {
    display: inline-block;
    position: fixed;
    width: 100%;
  }
  
    .my-card {
    display: inline-block;
    height: 20rem;
    width: 20%;
    position: relative;
    z-index: 1;
    opacity: 0;
    cursor: pointer;
    pointer-events: none;
    background: #2e5266;
    background: linear-gradient(to top, #2e5266, #6e8898);
    transition: 1s;
  }
  
  
 .my-card.active {
    z-index: 3;
    display: inline-block;
    opacity: 1;
    pointer-events: auto;
    transition: 1s;
  }
  
 .my-card.prev,.my-card.next {
    z-index: 2;
    display: inline-block;
    opacity: 0.6;
    pointer-events: auto;
    transition: 1s;
  }
