  .overlay {
      position: fixed;
      z-index: 1;
      top: 0;
      right: 0;
      overflow-x: hidden;
      transition: 0.5s;
      height: 100%;
    }
  .blackbg{
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0, 0.9);
  }  
  .overlay-content {
    position: relative;
    top: 25%;
    width: 100%;
    text-align: center;
    margin-top: 30px;
  }
  
  .overlay a {
    padding: 8px;
    text-decoration: none;
    font-size: 36px;
    color: #818181;
    display: block;
    transition: 0.3s;
  }
  
  .overlay a:hover, .overlay a:focus {
    color: #f1f1f1;
  }
  
  .overlay .closebtn {
    position: absolute;
    top: 20px;
    right: 45px;
    font-size: 60px;
  }
  
  @media screen and (max-height: 450px) {
    .overlay a {font-size: 20px}
    .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    left: 35px;
    }
  }

  .pure-black-bg{
    background: black;
  }

  .pure-white-bg{
    background: white;
  }

  .content-white{
    color: white;
  }
  .content-black{
    color: black;
  }

  .large-font{
    font-size: 3em;
  }
  .medium-font{
    font-size: 1em;
  }
