:root {
    --box-x-padding: 2rem;
    --box-y-padding: 2rem;
  }
  
  @media (min-width: 55em) {
    :root {
      --box-x-padding: 3rem;
      --box-y-padding: 6rem;
    }
  }
  @media (min-width: 55em) {
    .card-section {
      display: grid;
      grid-template-rows: repeat(3, auto);
      grid-template-columns: 2fr 2fr 6fr;
      grid-template-areas: ' . . title' 'img img content' 'img img .';
      max-width: 700px;
      margin-right: auto;
      margin-left: auto;
    }
    .card-section::after {
      content: '';
      grid-column: 2/-1;
      grid-row: 1/3;
      position: relative;
      z-index: -1;
      background: #fff;
      box-shadow: 0 0.2em 1em rgba(61,48,41,0.2);
    }
    .card-section h1 {
      grid-area: title;
      padding-top: var(--box-y-padding);
      padding-left: var(--box-x-padding);
    }
    .card-content {
      grid-area: content;
      padding-bottom: var(--box-y-padding);
      padding-left: var(--box-x-padding);
    }
    .img {
      grid-area: img;
    }
  }
  
  .card-section h1 {
    font-size: 2.5em;
    margin: 0 0 1rem;
    font-family: serif;
    position: relative;
  }
  
  @media (min-width: 40em) {
    .card-section h1::after {
      left: var(--box-x-padding);
      top: var(--box-y-padding);
    }
  }
  .card-content {
    color: #4d4d4d;
    margin-bottom: var(--box-y-padding);
  }
  @media (min-width: 40em) {
    .card-content {
      margin-bottom: 0;
    }
  }
  p {
    margin-top: 0;
    margin-bottom: var(--box-y-padding);
    line-height: 1.5;
    margin-block-end: 0px;
  }
  .card-content a {
    text-decoration: none;
    border: 1px solid #b3b3b3;
    border-radius: 1em;
    text-transform: uppercase;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 0.05em;
    color: inherit;
    display: inline-block;
    background-color: #fff;
  }
  .img {
    position: relative;
  }
  .img a {
    position: absolute;
    left: 0;
    bottom: 0;
  }
  img {
    max-width: 100%;
    vertical-align: middle;
  }